import "bootstrap/dist/css/bootstrap.min.css";
import { useEffect } from "react";
import useDroppableArea from "./hooks/useDroppableArea";
import "./App.css";
import "./styles/variables.css";
import RevisionFloat from "./pages/revisionFloat/revisionFloat";
import PlantAssignment from "./pages/plantAssignment/plantAssignment";

const App = ({ setGlobalHandleDrop, widgetType }) => {
  const { handleDrop } = useDroppableArea();

  useEffect(() => {
    if (handleDrop) {
      setGlobalHandleDrop(handleDrop); // Pass handleDrop to the global variable
    } else {
      console.error("[App] handleDrop is undefined.");
    }
  }, [handleDrop, setGlobalHandleDrop]);

  return;
  <>
    {widgetType === "munish-widget" && <RevisionFloat />}{" "}
    {widgetType === "Jatin_PlantAssignment" && <PlantAssignment />}
  </>;
};

export default App;
